<template>
  <v-container>
      <v-btn color="cred" @click="goBack" class="me-1"><v-icon>mdi-chevron-left</v-icon>Back</v-btn>
    
      <router-link to="" >
        <v-btn v-if="!loggedUser" color="info" class="me-1" @click="toggleLike">
          <v-icon>
            {{ 'mdi-heart-outline' }}
          </v-icon>Add to Wishlist
        </v-btn>
        <v-btn v-else color="info" class="me-1" @click="toggleLike">
          <v-icon :color="heartColor">
            {{ isLiked ? 'mdi-heart' : 'mdi-heart-outline' }}
          </v-icon>Add to Wishlist
        </v-btn>
      </router-link>

    <router-link to="">
      <v-btn color="success" class="me-1"><v-icon>mdi-map-marker</v-icon>Google Location</v-btn>
    </router-link>
    <v-row>
      <v-col cols="12" md="6">
        <v-tabs-items v-model="tabs" v-if="dealdetail.images.length > 0">
          <v-tab-item v-for="(item,i) in dealdetail.images" :key="i" :value="'mobile-tabs-5-' + i">
            <v-card flat>
              <v-img :src="(url+item.hotdeal_img_url)" cover max-height="350" min-height="350" class="img-fluid"></v-img>
            </v-card>
          </v-tab-item>
        </v-tabs-items>
        <div v-else>
          <v-img :src="('https://dummyimage.com/350x350&text='+dealdetail.hot_deal_title+' No Image')"
                 cover max-height="350" min-height="350" class="img-fluid">
          </v-img>
        </div>
          <v-tabs v-if="dealdetail.images.length > 0" v-model="tabs" fixed-tabs class="mt-2" show-arrows>
            <v-tabs-slider></v-tabs-slider>
            <v-tab v-for="(item,i) in dealdetail.images" :key="i" :href="('#mobile-tabs-5-'+i)" class="primary--text">
              <v-img :src="(url+item.hotdeal_img_url)" cover max-width="75" height="75"></v-img>
            </v-tab>
          </v-tabs>
      </v-col>
      <v-col cols="12" md="6">
        <div class="mobile">
          <div class="small d-flex justify-content-between">
              <div class="px-md-5">
                  <h2 class="h5 mb-1">{{dealdetail.hot_deal_title}}</h2>
                  <div class="mb-1">By
                    <router-link to="" class="text-decoration-none" :title=dealdetail.name>{{dealdetail.name}}</router-link>
                  </div>
                  <div><span class="cred fw-semibold">Price :</span> ₹{{dealdetail.price_from}} - ₹{{dealdetail.price_to}} ( Inclusive of all taxes )</div>
                  <div><span class="cred fw-semibold">Validity : </span> {{dealdetail.date_from}} - {{dealdetail.date_to}}</div>
              </div>
                <div class="mcontact">
                  <a :href="('tel:'+ dealdetail.mobile_phone)">
                     <v-btn fab small color="success" class="mb-2"><v-icon>mdi-whatsapp</v-icon></v-btn>
                  </a>
                  <a :href="('https://wa.me/'+dealdetail.mobile_phone+'?text=rf')">
                  <v-btn fab small color="primary"><v-icon>mdi-phone</v-icon></v-btn>
                  </a>
                </div>
          </div>
          <div class="accordion py-2" role="tablist">
             <b-card no-body class="mb-1">
                <b-card-header header-tag="header" class="p-1" role="tab">
                    <b-button class="w-100 catsy fw-bold cblu d-flex justify-content-between btn-sm" 
                    v-b-toggle.accordion-1 variant="light">Details</b-button>
                </b-card-header>
                <b-collapse id="accordion-1" visible accordion="my-accordion" role="tabpanel">
                   <b-card-body>
              <b-card-text>
                {{dealdetail.hot_deal_description}}
              </b-card-text>
            </b-card-body>
          </b-collapse>
      </b-card>
    </div>
      </div>
        <div class="desktop">
            <div class="px-md-5">
            <h2 class="h4 mb-0">{{dealdetail.hot_deal_title}}</h2>
            <div>By <router-link :to="{name:'businessprofile',params:{id:dealdetail.business_id, uname:dealdetail.name}}" class="text-decoration-none">{{dealdetail.name}}</router-link>
            </div>
            <div><span class="cred fw-semibold">Price :</span> ₹{{dealdetail.price_from}} - ₹{{dealdetail.price_to}} ( Inclusive of all taxes )</div>
            <div><span class="cred fw-semibold">Validity : </span> {{dealdetail.date_from}} - {{dealdetail.date_to}}</div>
            <div class="py-1">
              <a :href="('tel:'+ dealdetail.mobile_phone)"><v-btn color="cred" class="me-1">
                <v-icon>mdi-phone</v-icon>
                {{ dealdetail.mobile_phone }}</v-btn></a>
              <a :href="('https://wa.me/'+dealdetail.mobile_phone+'?text=rf')"><v-btn color="success" class="me-1"><v-icon>mdi-whatsapp</v-icon>WhatsApp</v-btn></a>
            </div>
            <p class="px-1 text-justify">
              {{dealdetail.hot_deal_description}}
            </p>
          </div>
        </div>
      </v-col>
    </v-row>
    <v-row class="mt-10">
      <h5 class="my-5"><span class="cblu bbred">Other Deals Near By</span></h5>
        <div class="mobile">
          <carousel v-if="ddeals.length > 0" :autoplay="false" :margin="20" :items="4" :dots="false" :slideBy="1"
                  :navText="['‹','›']"
                  :responsive = "{0:{items:1},576:{items:2},768:{items:3},1200:{items:4}}" class="deals mobile">
          <div v-for="(deal,i) in ddeals" :key="i" class="item mitem">
          <router-link :to="{name:'dealsdetail',params:{id:deal.id}}" class="link-dark small d-flex">
            <div class="mimg">
              <v-img v-if="deal.hotdeal_single_image.hotdeal_img_url!=null" :src=url+deal.hotdeal_single_image.hotdeal_img_url cover height="110" class="white--text align-start rounded-3"></v-img>
              <v-img v-if="deal.hotdeal_single_image.hotdeal_img_url==null" :src="('https://placehold.co/600x400?text='+deal.hot_deal_title)" cover height="110" class="white--text align-start rounded-3"></v-img>
            </div>
            <div class="mdetails">
                <div class="h5 mb-0">{{deal.hot_deal_title}}</div>
                <div v-if="!loggedUser">Add To Wishlist 
                    <v-icon>mdi-heart-outline</v-icon>
                </div>
                <div v-else>Add To Wishlist 
                    <v-icon v-if="deal.wishlist_status==1" color="red" small>mdi-heart</v-icon>
                    <v-icon v-else small>mdi-heart-outline</v-icon>
                </div>
                <div><span>Price :</span><span class="cred">₹{{ deal.price_from }}  - ₹{{ deal.price_to }}</span></div>
                <div class="fs11"><span>Validity : </span>{{ deal.date_from }} - {{ deal.date_to }}</div>
                <div><v-icon small>mdi-map-marker</v-icon>{{deal.city}}, {{deal.state}}</div>
            </div>
          </router-link>
          </div>
        </carousel>
      </div>
        <div class="desktop">
          <carousel v-if="ddeals.length > 0" :autoplay="false" :margin="20" :items="4" :dots="false" :slideBy="1"
                  :navText="['‹','›']"
                  :responsive = "{0:{items:1},576:{items:2},768:{items:3},1200:{items:4}}" class="deals">
          <div v-for="(deal,i) in ddeals" :key="i" class="item">
            <v-card @click="redirectUser(deal.id)">
              <v-img v-if="deal.hotdeal_single_image.hotdeal_img_url!=null" :src=url+deal.hotdeal_single_image.hotdeal_img_url cover height="150" class="white--text align-start"></v-img>
              <v-img v-if="deal.hotdeal_single_image.hotdeal_img_url==null" :src="('https://placehold.co/600x400?text='+deal.hot_deal_title)" cover height="150" class="white--text align-start"></v-img>
              <v-card-text class="link-dark">
                <div class="h5">{{deal.hot_deal_title}}</div>
                <div v-if="!loggedUser">Add To Wishlist 
                    <v-icon>mdi-heart-outline</v-icon>
                </div>
                <div v-else>Add To Wishlist 
                    <v-icon v-if="deal.wishlist_status==1" color="red" small>mdi-heart</v-icon>
                    <v-icon v-else small>mdi-heart-outline</v-icon>
                </div>
                <div><span>Price :</span><span class="cred">₹{{ deal.price_from }}  - ₹{{ deal.price_to }}</span></div>
                <div><span>Validity : </span> {{ deal.date_from }} - {{ deal.date_to }}</div>
                <div><v-icon small>mdi-map-marker</v-icon>{{deal.city}}, {{deal.state}}</div>
              </v-card-text>
            </v-card>
          </div>
        </carousel>
      </div>
    </v-row>

<v-snackbar v-model="snackbar" :timeout="timeout">{{ text }}
  <template v-slot:action="{}">
    <v-btn color="blue" text v-bind="attrs" @click="snackbar = false">Close</v-btn>
  </template>
</v-snackbar>


  </v-container>
</template>

<script>
import carousel from "vue-owl-carousel";
import axios from "axios";
export default {
  name: "DealDetail",
  components:{carousel},
  props:{
    id:Number,
  },
  data() {
    return {
      url:'https://bizlx.itechvision.in',
      // url:'http://localhost:8000',
      dealdetail:[],
      tabs: null,
      ddeals: [],
      isLiked: false,
      loggedUser: [],
      timeout: '',
      snackbar: false,
      text: '',
    };
  },
  // computed: {
  //   heartColor() {
  //     return this.isLiked ? 'red' : 'white';
  //   }
  // },
  created() {
    this.getDealdetail();
    this.loggedUser = this.$store.state.userData;
  },

  methods: {
    getDealdetail(){
     axios.get(`deals/details/${this.id}`)
         .then((resp)=>{
           this.dealdetail = resp.data.hotdeal;
           this.ddeals = resp.data.nearby;
           
            if(resp.data.hotdeal.wishlist_status==1){
              this.isLiked = 'red';
              this.heartColor = 'red';
            }

         });
    },
    goBack() {
      window.history.back();
    },
    redirectUser(hotDeal_id) {
      this.$router.push({ name: 'dealsdetail', params:{id:hotDeal_id} });
        axios.get(`deals/details/${hotDeal_id}`)
          .then((resp)=>{
            this.dealdetail = resp.data.hotdeal;
            this.ddeals = resp.data.nearby;
            
            if(resp.data.hotdeal.wishlist_status==1){
              this.isLiked = 'red';
              this.heartColor = 'red';
            }
            if(resp.data.hotdeal.wishlist_status==null){
              this.isLiked = '';
              this.heartColor = 'white';
            }
        });
     },

    toggleLike() {
      if(!this.loggedUser){
          this.snackbar = true;
          this.text = "Please login first.";
          this.timeout = 3000;
      }else{
        this.isLiked = !this.isLiked;
        let data = { 
          business_id: this.dealdetail.business_id,
          wishlist_type: 1,
          services_id: this.dealdetail.id,
        };
        if(this.isLiked==true){ // add 
            axios.post("add/wishlist", data)
                .then((resp) => {
                      this.snackbar = true;
                      this.text = resp.data.message;
                      this.timeout = 3000;
                      
                      this.isLiked = 'red';
                      this.heartColor = 'red';
                }
            );
        }
        if(this.isLiked==false){ // remove
            axios.post("add/wishlist", data)
                .then((resp) => {
                      this.snackbar = true;
                      this.text = resp.data.message;
                      this.timeout = 3000;
                      
                      this.isLiked = '';
                      this.heartColor = 'white';
                }
            );
        }
      }
    }
  },
  
}
</script>

<style scoped>
.item.mitem {
  box-shadow: 0 6px 10px -6px rgba(0,0,0,0.5);
  background: #f1f2f380;
  padding: 7px;
}
.mimg {
  width: 110px;
}
.mtitle {
  font-weight: 600;
  font-size: 12.25px;
}
.mdetails {
  width: 100%;
  padding: 0px 0 0 25px;
}
.mcontact {
  width: 42px;
}
.heart-filled {
  color: red;
}
.heart-unfilled {
  color: grey;
}
.fs11{font-size: 10.4px;}
@media(max-width: 576px){
  .v-btn:not(.v-btn--round).v-size--default {
	height: 30px;
	min-width: 64px;
	padding: 0 7px;
}
.btn.catsy.collapsed::after {
    content: '+';
    color: var(--cblu);
  }
  .btn.catsy.not-collapsed::after {
    content: '-';
    color: var(--cblu);
  }
}
</style>